.header {
	position: relative;
	height: 410px;
	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-image: url(../img/header-bg.jpg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		filter: blur(2px);
	}
	&::after {
		content: "";
		position: absolute;
		top: 101px;
		left: 50%;
		width: calc(100% - 10px);
		max-width: 1400px;
		height: 2px;
		transform: translateX(-50%);
		background-color: $white;
	}
}

.header__content {
	height: 100px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header__logo {
	font-size: 34px;
	color: $white;
}

.menu__logo,
.menu__descr,
.menu__social,
.menu__action,
.menu-close,
.nav__item_hide,
.burger {
	display: none;
}

.menu {
	display: flex;
	justify-content: flex-end;
	column-gap: 150px;
}

.menu__phone {
	& a {
		color: $white;
		font-size: 16px;
		font-weight: 600;
		transition: color 0.3s ease-in-out;
		&:hover {
			color: #b8b8b8;
		}
	}
}

.nav__list {
	display: flex;
	column-gap: 70px;
}

.nav__link {
	color: $white;
	font-size: 16px;
	line-height: 1;
	font-family: $fontFamily;
	font-weight: 600;
	text-transform: uppercase;
	transition: color 0.3s ease-in-out;
	&:hover {
		color: #b8b8b8;
	}
}

@media (max-width: 1025px) {
	._container_header {
		max-width: 1000px;
	}
	.header {
		height: 350px;
	}
	.menu {
		column-gap: 80px;
	}
	.nav__list {
		column-gap: 40px;
	}
}

@media (max-width: 961px) {
	.menu__phone {
		display: none;
	}
	.burger {
		display: block;
		position: fixed;
		right: 15px;
		z-index: 5;
		width: 50px;
		height: 50px;
		border: none;
		padding: 0px;
		background-color: $red-accent;
		cursor: pointer;
		&__line {
			background-color: #fff;
			position: absolute;
			height: 2px;
			width: 20px;
			right: 13px;
			top: 50%;
			transform: translateY(-50%);
			transition: opacity 0.3s ease-in-out;
			transition: all 0.3s ease-in-out;
		}
		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 50%;
			width: 25px;
			height: 3px;
			transform: translateX(-50%);
			transition:
				transform 0.3s ease-in-out,
				top 0.3s ease-in-out,
				bottom 0.3s ease-in-out;
			background-color: #fff;
		}
		&::before {
			top: 16px;
		}
		&::after {
			bottom: 16px;
		}
		&:hover .burger__line {
			right: 17px;
		}
	}
	.menu a {
		z-index: 105;
	}
	.menu {
		flex-direction: column;
		justify-content: flex-start;
		position: fixed;
		z-index: 100;
		left: 0;
		top: 0;
		width: 100%;
		height: 100vh;
		overflow-y: auto;
		padding: 30px;
		background-color: rgba(0, 0, 0, 1);
		transform: translateX(100%);
		transition: transform 0.6s ease-in-out;
	}
	.menu--visible {
		transform: translateX(0%);
	}

	.menu__logo {
		display: block;
		margin-bottom: 40px;
		font-size: 22px;
		font-family: "Kharkiv";
	}
	.nav__list {
		flex-direction: column;
		row-gap: 20px;
	}
	.nav__item_hide {
		display: block;
	}
	.nav__link {
		z-index: 110;
		font-size: 24px;
		font-weight: 400;
	}
	.menu__descr {
		display: block;
		margin-top: 200px;
		font-size: 24px;
		font-weight: 300;
	}
	.menu__social {
		display: block;
		margin-top: 30px;
	}
	.menu__social-link:first-child {
		margin-right: 20px;
	}
	.menu__action {
		display: flex;
		flex-wrap: wrap;
		column-gap: 5px;
		row-gap: 10px;
		margin-top: 30px;
	}
	.menu__action-link {
		z-index: 110;
		padding: 20px 40px;
		color: #fff;
		border: #fff solid 1px;
		font-size: 16px;
		text-transform: uppercase;
		transition: border ease-in-out 0.3s;
		&:hover {
			border-color: $red-accent;
		}
	}
	.menu-close {
		position: absolute;
		right: 30px;
		top: 30px;
		display: block;
		width: 3px;
		height: 25px;
		background-color: #fff;
		transform: rotate(45deg);
		&::after {
			content: "";
			position: absolute;
			right: -11px;
			top: 11px;
			display: block;
			width: 25px;
			height: 3px;
			background-color: #fff;
		}
	}
}

