._container_header {
	position: relative;
}
.show--menu {
	position: fixed;
	left: 50%;
	transform: translateX(-50%);
	max-width: $maxWidthContainer + px;
	width: 100%;
	z-index: 100;
	padding: 0 15px;
	background-color: #000;
}

@media (max-width: 961px) {
	.show--menu {
		background-color: transparent;
	}
	.show--menu .header__logo {
		display: none;
	}
}
